import { Component, ViewEncapsulation, Injector } from '@angular/core';
import { AppConfig } from '../app.config';
import { ChallengeService } from '../services/ChallengeService';
import 'rxjs/add/operator/map';
import { Http, Headers, RequestOptions } from '@angular/http';
declare var jQuery: any;
import { SessionStorage } from 'angular2-localstorage/WebStorage';
import { Select2OptionData } from 'ng2-select2';
import { __platform_browser_private__ } from '@angular/platform-browser'
import * as data from '../forms/elements/elements.data';
import { ProjectModels } from '../models/ProjectModels';
import { ChallengeModels } from '../Models/ChallengeModels';
import { UsersModel } from '../models/UsersModel';
@Component({
  selector: 'projectdashboard',
  template: require('./projectdashboard.template.html'),
  encapsulation: ViewEncapsulation.None,
  providers: [ChallengeService],
  styles: [require('./projectdashboard.style.scss'), require('./set1.css'), require('./set2.css'), require('./normalize.css')]
})
export class Projectdashboard {
  config: any;
  configFn: any;
  agency_id: string;
  projectCategoryTitle: string;
  project_color_palette: string;
  categories: Projectdashboard[] = [];
  advancedAdmins: Projectdashboard[] = [];
  private projectslist: ProjectModels[] = [];
  projectTitle: string;
  selectedvalue: string;
  cat_id: string;
  advancedAdmin_id: string;
  projectDescription: string;
  noOfProjects: number;
  current_userId: string;
  colorOptions: Object = { color: '#f0b518' };
  file: any;
  public userObject: UsersModel;
  


  constructor(config: AppConfig, private challengeService: ChallengeService, private http: Http) {
    this.configFn = config;
    this.config = config.getConfig();
    this.userObject = JSON.parse(sessionStorage.getItem('userObject'));
    this.challengeService.getAllProjects(this.userObject.agencyName, sessionStorage.getItem('token')).subscribe(
      a => {
        this.projectslist = a.data;
        this.noOfProjects = this.projectslist.length;
      }
    );



    this.current_userId = this.userObject._id;
    console.log(this.userObject, this.current_userId);
    this.challengeService.getProjectCategories(this.userObject.agencyName, sessionStorage.getItem('token'))
      .subscribe(
      a => {

        this.categories = a.data;
      }
      );


    this.challengeService.getAdvanceAdmins(this.userObject.agencyName, sessionStorage.getItem('token'))
      .subscribe(
      a => {
        this.advancedAdmins = a.data;
        //console.log(this.advancedAdmins);
      }
      );

    jQuery(document).ready(function ($) {
      jQuery("#mymodal2 button.close,#myModal2 .modal-footer>button").click(function () {

        jQuery("#myModal2 .modal2-success").hide();


      })
      $('#myModal .sign-in-btn').attr('disabled', true);


      $('#myModal .custom-input').keyup(function () {
        if (this.value.length < 8 || this.value.length > 30) {
          $('#myModal .sign-in-btn').attr('disabled', true);

        }
        else {
          $('#myModal .sign-in-btn').attr('disabled', false);

        }
      })

    })
  }



  ngOnInit(): void {
    jQuery('#colorpicker').colorpicker(this.colorOptions);
  }

  getImageFile(event) {
    this.file = event.target.files[0];
  }

  getCategoryId(value) {
    this.cat_id = value;
  }

  getAdvancedAdmins(value) {
    jQuery("#AdvancedAdmins option[value='" + this.current_userId + "']").remove();
    this.advancedAdmin_id = value;
  }

  addProjectCategory() {
    var project_category_title = this.projectCategoryTitle;
    var project_color_palette = jQuery("#colorpicker span>i").css("background-color");
    jQuery("#projectCategoryTitle").css("border", "2px solid black");
    if (this.projectCategoryTitle != "" && this.projectCategoryTitle.replace(/\s/g, '').length != 0) {
      this.challengeService.addProjectCategoryTitle(this.userObject.agencyName, this.projectCategoryTitle, project_color_palette, sessionStorage.getItem('token'))
        .subscribe(project_category_title => {
          jQuery("#myModal .close").click();
        });
    }
    else {
      jQuery("#projectCategoryTitle").css("border", "2px solid red");
    }
  }



  createProject() {
    var aa = document.getElementById("AdvancedAdmins");
    var pc = document.getElementById("projectCategory");

    var selectedValueaa = jQuery("#AdvancedAdmins").val();
    var selectedValuepc = jQuery("#projectCategory").val();



    if ((jQuery("#projectImage").val() == '') || this.projectTitle == "" || this.projectDescription == "" || selectedValueaa == "" || selectedValuepc == "") {
      jQuery("#myModal2 .modal2-errors").show();
      jQuery("#myModal2 .modal2-errors").text("Please fill out all the fields");



    } else {
      jQuery("#myModal2 .modal2-errors").hide();

      this.challengeService.createAgencyProject(this.file, this.projectTitle, this.userObject.agencyName, this.projectDescription, this.cat_id, this.advancedAdmin_id, sessionStorage.getItem('token'))
        .subscribe(

        a => {
          console.log(a);
          if (a.code == 200) {
            jQuery("#myModal2 .modal2-success").text("Project Created Successfully");
            jQuery("#myModal2 .modal2-success").show();
            jQuery("#myModal2 .modal2-errors").hide();
            jQuery("#myModal2 .custom-input").val('');

          } else if (300) {
            jQuery("#myModal2 .modal2-success").hide();
            jQuery("#myModal2 .modal2-errors").text("A project with the same name exists already");
            jQuery("#myModal2 .modal2-errors").show();
            jQuery("#myModal2 .custom-input").val('');
          }
        });
    }
  }
}



